@import "src/style/palette";

.stars {
  width: 100%;
  height: 50%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.section-sky__star {
  height: 0.18rem;
  width: 0.18rem;
  background-color: $primary-light;
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  animation: twinkle 2s infinite;
  animation-delay: calc(var(--star) * 0.05s);
}
