@import "src/style/z-index";
@import "src/style/palette";

/* SUN */

.section-sky__sun {
  background-color: $sun;
  height: var(--sun);
  width: var(--sun);
  border-radius: 50%;
  position: absolute;
  margin: var(--sun-space) 0 0 var(--sun-space);
  box-shadow: 0 0 0 0.62em rgba($sun, 0.6), 0 0 0 1.25em rgba($sun, 0.5),
    0 0 0 1.9em rgba($sun, 0.4);
}
